/* FONT SUPREME */
@font-face {
  font-family: $font-clash-display;
  src: url("../fonts/clashDisplay-regular.woff2") format("woff2"),
    url("../fonts/clashDisplay-regular.woff") format("woff");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}

